#intro {

    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;

}

.bg{
    position: absolute;
    top: -4rem;
    right: 0;
    z-index: -1; /*-1 so that,  can go behind the intro */ 
    object-fit: cover;
    height: 100vh;
}

.introContent {
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hello {
    font-size: 1.75rem;
    font-weight: 100;
}

.introName {
    color: yellow;
}

.introPara {
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}

.btn {
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
    cursor: pointer;
}

.btnImg {
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}

.profile-details-role{
    display: flex;
    flex-direction: column;
    margin: 14px 0 24px 0;
}

.profile-details-role h3{
    font-size: 48px;
    font-family: cursive;
}

.primary-text{
    color: (--white);
}

.highlighted-btn {
    cursor: pointer;
}

.btnSpace {
    display: flex;
    gap: 20px;
}

@media screen and (max-width:840px) {
    .bg{
        right: -10vw;
    }
    .introContent {
        font-size: 10vw;
    }
    .hello{
        font-size: 4.5vw;
    }
}

@media screen and (max-width:480px) {
    .bg{
        right: -10vw;
    }

}